$pointClr : #ff4747;

.header {
  position: relative;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding:50px 0;

  .logo {
    position: absolute;
    top:center;
    left:0;
    .logoImg {
      width:50px;
    }
  }

  .navBtn {
    display: none;
  }

  .menuWrap {
    display: flex;
    justify-content: center;
    align-items: center;
    .menu {
      display: flex;
      justify-content: center;
      align-items: center;
      li {
        a {
          display: block;
          font-size:20px;
          font-weight:bold;
          margin:0 20px;
          color: #333;
        }
        a.active, &:hover a {
          position: relative;
          color: $pointClr;
          &:after {
            display: block;
            content:"";
            position:absolute;
            bottom:-5px;
            left:0;
            width: 100%;
            height:2px;
            background: $pointClr;
          }
        }
      }
    }
  }

  .icon {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left:20px;
    button {
      width: 40px;
      height: 40px;
      cursor: pointer;
      .icoMoon {
        font-size: 34px;
        color: #febc06;
      }
    }
    button + button {
      margin-left: 15px;
    }
  }
}

/* Tablet */
@media only screen and (max-width: 1200px) {
  
}

/* Mobile */
@media only screen and (max-width: 800px) {
  .header {
    padding: 10vw;
    .logo {
      position: absolute;
      top:center;
      left:0;
      .logoImg {
        width:15vw;
      }
    }

    .navBtn {
      display: block;
      position: absolute;
      top: 7vw;
      right: 3vw;
      font-size: 6vw;
    }

    .menuWrap {
      z-index: 5;
      flex-direction: column;
      justify-content: space-between;
      position: fixed;
      top:0;
      right:0;
      width:100vw;
      height:100vh;
      background-color: #FFF;
      transition: 0.7s;
      transform: translateX(100vw);
      .menu {
        flex-direction: column;
        height: 85vh;
        li {
          a {
            
            font-size:8vw;
            margin:10vw;
          }
          a.active, &:hover a {
            &:after {
              display: none;
            }
          }
        }
      }

      &.navToggle {
        transform: translateX(0);
      }

      .icon {
        position: absolute;
        bottom:15vw;
        left:0;
        right: 0;
        margin-left:0;
        button {
          width: 15vw;
          height: 15vw;
          cursor: pointer;
          .icoMoon {
            font-size: 15vw;
            color: #febc06;
          }
        }
        button + button {
          margin-left: 10vw;
        }
      }
    }
  }
}