.work {
  .workWrap {
    width: 95%;
    max-width: 1000px;
    margin:auto;

    .workList {
      border-bottom: 1px solid #BBB;
      box-sizing: border-box;
      p {
        padding: 20px;
        font-size: 22px;
        font-weight: 600;
        display: flex;
        justify-content: space-between;
        align-items: center;
        cursor: pointer;
        &:hover {
          background-color: $pointClr;
          color:#FFF;
        }
        .icoPlus {display: block;}
        .icoMinus {display: none;}
      }

      .workBox {
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        height: 0;
        overflow: hidden;
        box-sizing: border-box;
        .imgWrap {
          position: relative;
          width: 40%;
          margin: 20px;
          .linkWrap {
            display: none;
            justify-content: center;
            align-items: center;
            width: 100%;
            height: 100%;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            background-color: rgba(0,0,0,0.6);
            .btnView {
              display: block;
              width: 150px;
              height: 40px;
              line-height: 40px;
              border:1px solid #FFF;
              border-radius:20px;
              text-align: center;
              font-weight: 600;
              color: #FFF;
              z-index: 4;
              &:hover {
                background-color: $pointClr;
                border:1px solid $pointClr;
              }
            }
          }
          
          &:hover .linkWrap {display: flex;}

        }
        .workText {
          padding: 20px;
          width: 60%;
          height: 100%;
          strong {
            font-size:20px;
            font-weight: 600;
          }
        }
      }

      &.on {
        > p {
          .icoPlus {display: none;}
          .icoMinus {display: block;}
        }
      }
      &.on > .workBox {
        height: auto;
        border-top: 1px solid #BBB;
      }
    }
  }
}